.loader-container {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 40vh;
}

.custom-loader {
	border: 12px solid #f3f3f3;
	border-top: 12px solid #3498db;
	border-radius: 50%;
	width: 80px;
	height: 80px;
	animation: spin 2s linear infinite;
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
